import classNames from 'classnames';
import React, { useRef } from 'react';
import p1 from '../../assets/imgs/partner1.png';
import p10 from '../../assets/imgs/partner10.png';
import p11 from '../../assets/imgs/partner11.png';
import p2 from '../../assets/imgs/partner2.png';
import p3 from '../../assets/imgs/partner3.png';
import p5 from '../../assets/imgs/partner5.png';
import p6 from '../../assets/imgs/partner6.png';
import p7 from '../../assets/imgs/partner7.png';
import p8 from '../../assets/imgs/partner8.png';
import p9 from '../../assets/imgs/partner9.png';
import ScrollTrigger from "../../components/scrollTrigger";
import { I } from "../../utils";
import { useViewport } from '../useViewport';
import * as styles from './index.module.less';

export default function App() {
  const { size } = useViewport();
  const [animate, setAnimate] = React.useState(false);
  const isM = size !== 'l';
  const ref = useRef();
  const onEnterViewport = () => {
    if (animate == false) {
      setAnimate(true)
    }
  }
  const onExitViewport = () => {
    setAnimate(false)
  }
  const list = [p1, p2, p3, p5, p6, p7, p8, p9, p10, p11]
  return (
    <div id="partner" className={styles.partner}>
      <ScrollTrigger
        progress={0.1}
        onEnter={onEnterViewport}
        // onExit={onExitViewport}
        className={classNames([styles.partnerContainer], { [styles.show]: animate })}>
        <h1 style={{ animationDelay: '0ms' }} className={classNames(styles.line, styles.title, { [styles.show1]: animate })}>{I('partner.title')}</h1>
        <div className={styles.container}>
          {list.map((p, i) => <div
            key={i}
            style={{
              animationDelay: `${parseInt(i / 5 * 2) + 1}00ms`,
              width: isM ? '40%' : 'calc(20% - 20px)'
            }}
            className={classNames(styles.line, styles.cell, { [styles.show1]: animate })}
          >
            <img src={p} />
          </div>)}
        </div>
      </ScrollTrigger>
    </div>
  )
}
