import classNames from 'classnames';
import { default as React, useCallback } from "react";
import ScrollTrigger from 'react-scroll-trigger';


const App = ({ className, onExit, onEnter, progress = 0.1, onProgress, children }) => {
  let show = false;
  const ref = React.useRef();
  const onExitViewport = useCallback(() => {
    show = false;
    onExit && onExit();
  }, []);
  const onShow = useCallback((data) => {
    onProgress && onProgress({
      ...data,
      height: ref.current?.element?.clientHeight,
      windowHeight: document.documentElement.clientHeight || document.body.clientHeight
    })
    if (data.progress > progress) {
      show = true;
      onEnter && onEnter();
    }
  }, [ref?.current, onProgress]);
  return (
    <ScrollTrigger
      // onEnter={onEnterViewport}
      ref={ref}
      onExit={onExitViewport}
      className={classNames(className)}
      onProgress={data => {
        onShow(data);
      }}
    >
      {children}
    </ScrollTrigger>
  )
}

export default App
