import classNames from 'classnames';
import React, { useRef } from 'react';
import ScrollTrigger from "../../components/scrollTrigger";
import { I } from "../../utils";
import { useViewport } from '../useViewport';
import * as styles from './index.module.less';

export default function App({ setNavColor }) {
  const { size } = useViewport();
  const isM = size !== 'l';
  const [animate, setAnimate] = React.useState(false);
  const ref = useRef();
  const onEnterViewport = () => {
    if (animate == false) {
      setAnimate(true)
    }
  }
  const onProgress = (d) => {
    console.log('d.progress', d.progress)
    if (d.progress >= 0.98) {
      setNavColor('#0e1d42');
    } else {
      setNavColor('#fff');
    }
  }
  return (
    <div id="about" ref={ref} className={styles.page}>
      <ScrollTrigger
        progress={0.1}
        onEnter={onEnterViewport}
        onProgress={onProgress}
      >
        <div
          className={classNames(styles.about, { [styles.show]: animate })}
          style={{ flexDirection: isM ? 'column' : 'row' }}>
          <div style={{ marginRight: '4rem' }}>
            <h1 style={{ animationDelay: '0ms' }} className={classNames(styles.line, styles.title, { [styles.show1]: animate })}>{I('nav.aboutUs')}</h1>
            <p style={{ animationDelay: '100ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>
              {I('about.text1')}
              <div>{I('about.text2')}</div>
            </p>
            <p style={{ animationDelay: '200ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>
              {I("about.text3")}
            </p>
            <p style={{ animationDelay: '300ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>
              {I("about.text4")}</p>
            <p style={{ animationDelay: '400ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>
              {I('about.text5')}
            </p>
          </div>
          <div>
            <h2 style={{ animationDelay: '0ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>{I('about.text6')}</h2>
            <h3 style={{ animationDelay: '0ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>
              {I('about.text7')}</h3>
            <div style={{ animationDelay: '100ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>
              {I('about.text8')}
            </div>
            <h3 style={{ animationDelay: '200ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>{I('about.text9')}</h3>
            <div style={{ animationDelay: '300ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>{I('about.text10')}</div>
            <h3 style={{ animationDelay: '400ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>{I('about.text11')}</h3>
            <div style={{ animationDelay: '500ms' }} className={classNames(styles.line, { [styles.show1]: animate })}>{I('about.text12')}</div>
          </div>
        </div>
      </ScrollTrigger>
    </div>
  )
}
