
import React, { useRef } from "react";
import logo from '../../assets/imgs/logo3@2x.png';
import { useViewport } from '../useViewport';
import * as styles from './index.module.less';
export default function App() {
  const { size, width, height, scrollTop } = useViewport();
  const [animate, setAnimate] = React.useState(true);
  const isM = size !== 'l';
  const ref = useRef();
  const onEnterViewport = () => {
    console.log('animate', animate)
    if (!isM) {
      setAnimate(true)
    }
  }
  const onExitViewport = () => {
    setAnimate(false)
  }
  let arf = (height / 2 - scrollTop) / (height || 1);
  arf = arf > 0.8 ? 0.8 : arf || 1;
  const style = {
    backgroundColor: `rgba(47,98,145,${arf})`
  }
  return (
    <div id="banner" className={styles.banner} ref={ref} style={{ position: 'relative', height: height }}>
      <img className={styles.img} src={logo} style={{ animationDelay: '50ms' }} />
      <div className={styles.title} style={{ animationDelay: '200ms' }}>
        Lionline is committed to building a professional level invest company
      </div>
      <div className={styles.title} style={{ animationDelay: '300ms' }}>
        Served 30 + companies at home and abroad
      </div>


      <div className={styles.text} style={{ animationDelay: '400ms' }}>
        including public chain projects, alliance chain projects, protocol level projects, DeFi, blockchain game applications, other DAPP and exchange platforms
      </div>
    </div>
  )
}