import classNames from 'classnames';
import React, { useRef } from 'react';
import p1 from '../../assets/imgs/portfolio1.png';
import p10 from '../../assets/imgs/portfolio10.png';
import p11 from '../../assets/imgs/portfolio11.png';
import p12 from '../../assets/imgs/portfolio12.png';
import p13 from '../../assets/imgs/portfolio13.png';
import p14 from '../../assets/imgs/portfolio14.png';
import p16 from '../../assets/imgs/portfolio16.png';
import p17 from '../../assets/imgs/portfolio17.png';
import p18 from '../../assets/imgs/portfolio18.png';
import p2 from '../../assets/imgs/portfolio2.png';
import p21 from '../../assets/imgs/portfolio21.png';
import p3 from '../../assets/imgs/portfolio3.png';
import p6 from '../../assets/imgs/portfolio6.png';
import p7 from '../../assets/imgs/portfolio7.png';
import p8 from '../../assets/imgs/portfolio8.png';
import p9 from '../../assets/imgs/portfolio9.png';
import ScrollTrigger from "../../components/scrollTrigger";
import { I } from "../../utils";
import { useViewport } from '../useViewport';
import * as styles from './index.module.less';


export default function App() {
  const { size } = useViewport();
  const [animate, setAnimate] = React.useState(false);
  const isM = size !== 'l';
  const ref = useRef();
  const onEnterViewport = () => {
    console.log('project animate', animate)
    if (animate == false) {
      setAnimate(true)
    }
  }
  const onExitViewport = () => {
    setAnimate(false)
  }
  const coins = {
    'https://hoprnet.org/': p1,
    'https://www.nucypher.com/': p2,
    'https://findora.org/': p3,
    // "https://raydium.io/": p4,
    // "https://www.cere.network/": p5,
    "https://reef.finance/": p6,
    "https://nodle.io/": p7,
    "http://coinversation.io/": p8,
    "https://kine.io/": p9,
    "https://virtue.poker/": p10,
    "https://www.marlin.org/": p11,
    "https://dfinity.org/": p12,
    "https://taraxa.io/": p13,
    "https://www.onflow.org/": p14,
    // "https://rocki.app/": p15,
    "https://polkadot.network/": p16,
    "https://minaprotocol.com/": p17,
    "https://acala.network/": p18,
    "https://portaldefi.com/": p21,
    // "https://knit.finance/": p19,
    // "https://trustbase.network": p20
  }
  return (
    <div id="project" className={styles.project}>
      <ScrollTrigger
        progress={0.1}
        onEnter={onEnterViewport}
        // onExit={onExitViewport}
        className={classNames([styles.projectContainer], { [styles.show]: animate })}>
        <h1 style={{ animationDelay: '0ms' }} className={classNames(styles.line, styles.title, { [styles.show1]: animate })}>{I('nav.project')}</h1>
        <div className={styles.container}>
          {Object.keys(coins).map((k, i) => <a
            target="_blank"
            href={k}
            key={k}
            style={{
              animationDelay: `${parseInt(i / 5 * 2) + 1}00ms`,
              width: isM ? '40%' : 'calc(20% - 20px)'
            }}
            className={classNames(styles.line, styles.cell, { [styles.show1]: animate })}>
            <img src={coins[k]} />
          </a>)}
        </div>
      </ScrollTrigger>
    </div>
  )
}